function main() {
  const hamburgerButton: HTMLButtonElement = (() => {
    const elem = document.getElementById("hamburger")
    if (elem instanceof HTMLButtonElement) {
      return elem
    }

    throw new Error("Expected there to be a button#hamburger tag")
  })()

  const mainNavContainer: HTMLDivElement = (() => {
    const elem = document.getElementById("main-nav-container")
    if (elem instanceof HTMLDivElement) {
      return elem
    }

    throw new Error("Expected there to be a div#main-nav-container tag")
  })()

  const navOverlay: HTMLDivElement = (() => {
    const elem = document.getElementById("nav-overlay")
    if (elem instanceof HTMLDivElement) {
      return elem
    }

    throw new Error("Expected there to be a div#nav-overlay tag")
  })()

  const closeMenu = () => {
    mainNavContainer.classList.remove("show")
    hamburgerButton.classList.remove("show")
    navOverlay.classList.remove("show")
  }

  hamburgerButton.addEventListener("click", () => {
    toggleClass("show", mainNavContainer)
    toggleClass("show", hamburgerButton)
    toggleClass("show", navOverlay)
  })

  mainNavContainer.addEventListener("click", (e) => {
    const target = e.target as HTMLElement
    const link = target.closest("a")
    if (link) closeMenu()
  })
}

function toggleClass(className: string, element: HTMLElement) {
  if ([...element.classList.values()].includes(className)) {
    element.classList.remove(className)
  } else {
    element.classList.add(className)
  }
}

if (document.readyState === "complete") {
  main()
} else {
  document.addEventListener("DOMContentLoaded", main)
}
